import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, Input, OnInit, Renderer2, ViewChild } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { Translations } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-ad-description',
    templateUrl: './ad-description.component.html',
    styleUrls: ['./ad-description.component.scss'],
    standalone: false
})
export class AdDescriptionComponent implements OnInit, AfterViewInit {
	constructor(
		public viewport: ViewportService,
		private cdr: ChangeDetectorRef,
		private renderer2: Renderer2,
		private readonly sanitize: DomSanitizer
	) {}

	@Input() set description(data: string) {
		// Render an safe html for decription content
		this.descriptionContent = this.sanitizeHtml(data);
	}
	@Input() translations: Translations;
	@Input() isAgentHub = false;

	@ViewChild('descriptionContainer', { static: true }) descriptionContainer: ElementRef;

	readonly buttonType = ButtonType;
	showMore = true;
	overflowContent: boolean;
	descriptionContent: SafeHtml;

	toggleShowMore(): void {
		this.showMore = !this.showMore;

		if (this.showMore) {
			this.renderer2.setStyle(this.descriptionContainer.nativeElement, 'max-height', 'unset');
		} else {
			this.renderer2.setStyle(this.descriptionContainer.nativeElement, 'max-height', '160px');
		}
	}

	checkContentHeight(): void {
		if (this.descriptionContainer.nativeElement.scrollHeight > 160) {
			this.overflowContent = true;
			this.cdr.detectChanges();
		}
	}

	sanitizeHtml(content: string): SafeHtml {
		return this.sanitize.bypassSecurityTrustHtml(content);
	}

	ngAfterViewInit(): void {
		this.checkContentHeight();
	}

	ngOnInit(): void {}
}
