import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';

import { Ad, Translations } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import { ButtonType } from '@shared/components/button/button.component';
import YouTubePlayer from 'youtube-player';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

// TODO: Refactor and optimize the codes here.
@Component({
    selector: 'findqo-ad-video',
    templateUrl: './ad-video.component.html',
    styleUrls: ['./ad-video.component.scss'],
    standalone: false
})
export class AdVideoComponent implements OnInit {
	constructor(
		public viewport: ViewportService,
		private sanitizer: DomSanitizer
	) {}

	@Input() translations: Translations;
	@Input() ad: Ad;
	@Input() isAgentHub = false;
	@ViewChild('videoPlayer', { static: false }) videoplayer: ElementRef;

	readonly buttonType = ButtonType;
	isVideoPlaying = false;
	videoId: string;
	videoType = false;
	safeUrl: SafeResourceUrl;

	checkVideoType(): void {
		const VIDEO_URL = this.ad?.youtubeVideoUrl;
		if (VIDEO_URL && (VIDEO_URL.includes('youtube') || VIDEO_URL.includes('youtu.be'))) {
			this.videoType = true;
		} else {
			this.videoType = false;
		}
	}

	videoTour(): void {
		let player;
		const VIDEO_URL: any = this.ad.youtubeVideoUrl;
		this.safeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(VIDEO_URL.includes('vimeo') ? this.parseVimeoUrl(VIDEO_URL) : VIDEO_URL);

		if (VIDEO_URL) {
			const YOUTUBE_URL = new URL(VIDEO_URL);

			try {
				if (this.videoType) {
					if (VIDEO_URL.includes('/shorts/') || VIDEO_URL.includes('/youtu.be/')) {
						this.videoId = this.extractYoutubeVideoId(VIDEO_URL);
					} else {
						this.videoId = YOUTUBE_URL.searchParams.get('v');
					}

					if (this.videoId) {
						player = YouTubePlayer('video-player');
						player.loadVideoById(this.videoId);
						player.playVideo();
						this.isVideoPlaying = true;
					}
				} else {
					this.isVideoPlaying = true;
				}
			} catch (error) {}
		}
	}

	private extractYoutubeVideoId(shortsUrl: string): string | null {
		const MATCH = shortsUrl.match(
			/(?:https?:\/\/)?(?:www\.)?(?:youtube\.com\/(?:[^\/\n\s]+\/\S+\/|(?:v|e(?:mbed)?)\/|\S*?[?&]v=|shorts\/)|youtu\.be\/)([a-zA-Z0-9_-]{11})/
		);

		return MATCH ? MATCH[1] : null;
	}

	private parseVimeoUrl(url: string): string {
		const REGEX = /https:\/\/(?:player\.)?vimeo\.com\/(?:video\/)?(\d+)/;
		const MATCHES = REGEX.exec(url);
		return MATCHES && 'https://player.vimeo.com/video/' + MATCHES[1];
	}

	ngOnInit(): void {
		this.checkVideoType();
	}
}
