import { Component, Input, OnInit } from '@angular/core';

import { Translations } from '@core/models';
import { ViewportService } from '@core/services/viewport.service';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-ad-extras',
    templateUrl: './ad-extras.component.html',
    styleUrls: ['./ad-extras.component.scss'],
    standalone: false
})
export class AdExtrasComponent implements OnInit {
	constructor(public viewport: ViewportService) {}
	@Input() extras: string[];
	@Input() section: string;
	@Input() translations: Translations;
	@Input() isAgentHub = false;

	showMore = true;
	titleId: string;

	readonly buttonType = ButtonType;

	toggleShowMore(): void {
		this.showMore = !this.showMore;
	}

	ngOnInit(): void {
		if (this.section === 'Motors') {
			this.titleId = 'n545';
		} else {
			this.titleId = 'n680';
		}
	}
}
